* {
  margin: 0;
  padding: 0;
  font-size: 14px;
  font-weight: bold;
}

ol, ul, li {
  list-style: none;
}

a {
  text-decoration: none;
}

.container {
  position: relative;
  overflow: hidden;
}

.container nav {
  position: absolute;
  top: 0px;
  left: 120px;
  z-index: 1;
  animation: fadein 1s forwards;
  animation-delay: 1.5s !important;
  opacity: 0;
}

@keyframes fadein {
  0% {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
    opacity: 0;
  }
  100% {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
    opacity: 1;
  }
}

.container nav ul li {
  float: left;
  width: 100px;
}

.container nav ul li a {
  display: block;
  color: #fff;
  font-size: 40px;
  text-decoration: underline;
}

.bg__wrap {
  position: relative;
  float: left;
  width: 25%;
}

.bg__wrap .bg1 {
  height: 900px;
